  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .nav {
    margin-top: 60px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  .nav__list {
    display: flex;
    flex-flow: column wrap;
    height: calc(85vh - 60px);
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  .nav__list--active {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  .nav__item {
    width: 100px;
    flex: 1;
    position: relative;
  }
  .nav__link {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    text-decoration: none;
    font-size: 24px;
    background: #2b3033;
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  .nav__link:hover {
    background: #272b2e;
  }
  @media (max-width: 640px) {
    .nav {
      width: 70px;
    }
    .nav__list {
      height: 90vh;
    }
  }
  
  .burger {
    height: 15vh;
    width:100px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    background: #2b3033;
    cursor: pointer;
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  .burger:hover {
    background: #272b2e;
  }
  .burger__patty {
    position: relative;
    height: 2px;
    width: 40px;
    background: white;
  }
  .burger__patty:before {
    content: "";
    position: absolute;
    top: -10px;
    left: 0;
    height: 2px;
    width: 100%;
    background: white;
  }
  .burger__patty:after {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;
    height: 2px;
    width: 100%;
    background: white;
  }
  .burger__patty, .burger__patty:before, .burger__patty:after {
    will-change: transform;
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  .burger--active .burger__patty {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  .burger--active .burger__patty:before {
    -webkit-transform: rotate(-45deg) translate(-7px, -7px) scaleX(0.7);
            transform: rotate(-45deg) translate(-7px, -7px) scaleX(0.7);
  }
  .burger--active .burger__patty:after {
    -webkit-transform: rotate(45deg) translate(-7px, 7px) scaleX(0.7);
            transform: rotate(45deg) translate(-7px, 7px) scaleX(0.7);
  }
  @media (max-width: 640px) {
    .burger {
      height: 10vh;
    }
    .burger__patty {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
    }
    .burger--active .burger__patty {
      -webkit-transform: scale(0.8) rotate(90deg);
              transform: scale(0.8) rotate(90deg);
    }
  }
  
  .panel {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }
  .panel__wrapper {
    padding: 7vh 7vw;
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
  .panel__content {
    will-change: transform;
    -webkit-transform: scale(0.7) rotateX(-230deg);
            transform: scale(0.7) rotateX(-230deg);
    -webkit-transform-origin: center 80%;
            transform-origin: center 80%;
    opacity: 0;
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    margin-left:40px;
  }
  .panel__content--active {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
  .panel__headline {
    font-weight: 700;
    opacity: 0.8;
    font-size: 48px;
    margin: 0 0 25px 0;
  }
  .panel p {
    margin: 0 0 25px 0;
    color: #1a1a1a;
    font-size: 24px;
    max-width: 800px;
  }
  .panel p:last-child {
    margin-bottom: 0;
  }
  .panel__block {
    height: 3px;
    background: rgba(0, 0, 0, 0.2);
    margin: 0 0 25px 0;
  }
  @media (max-width: 640px) {
    .panel__headline {
      font-size: 36px;
    }
    .panel__wrapper {
      padding: 10vh 10vw;
    }
    .panel p {
      font-size: 16px;
    }
  }
  
